.header__menu-item {
  position: absolute;
  width: 100%;
  height: 3px;
  top: 9px;
  left: 0;
  background-color: #fff;
  transition: all .4s ease 0s;
}

.header__menu-toggle:checked ~ .header__logo-container > .header__menu > .header__menu-item {
  transform: rotate(-45deg);
  top: 9px;
}

